import React from 'react';
import classNames from 'clsx';
import { ImageRendererProps } from '../types';
import { ResizedImageSource } from '../../lib';
import { PrintOnlyImageSource } from './printOnlySource';
import s from './DefaultImageRenderer.module.scss';

export const DefaultImageRenderer: React.FC<ImageRendererProps> = ({
  data,
  selectors,
}) => {
  const focalPoint = data.metaData.focalPoint || { x: 0.5, y: 0.5 };
  const styleVariables = {
    '--backgroundPosition': `${focalPoint.x * 100}% ${focalPoint.y * 100}%`,
  } as React.CSSProperties;

  if (typeof data.mediaUrl === 'string') {
    return (
      <picture>
        <img
          className={classNames(s.image, selectors?.image)}
          src={data.mediaUrl}
          alt={data.metaData.title}
          style={styleVariables}
        />
      </picture>
    );
  } else if (typeof data.mediaUrl === 'object') {
    return (
      <picture id={`multi_picture_${data.id}`} key={`multi_picture_${data.id}`}>
        {data.mediaUrl.map((src: ResizedImageSource, index: number) =>
          src.forPrinting ? (
            <PrintOnlyImageSource
              key={`print-only-image-${index}`}
              srcSet={src.dpr}
              type={`image/${src.type}`}
            />
          ) : (
            <source
              key={`image-source-${index}`}
              srcSet={src.dpr || src.url}
              type={`image/${src.type}`}
            />
          ),
        )}
        <img
          className={classNames(s.image, selectors?.image)}
          src={data.mediaUrl[data.mediaUrl.length - 1].url}
          alt={data.metaData.title}
          style={styleVariables}
        />
      </picture>
    );
  } else {
    return null;
  }
};
